    <n-config-provider >
        <n-grid cols="2">
            <n-grid-item>
                <div class="box">
                        <n-grid cols="9" class="list">
                        <n-grid-item class="box">
                            <a href="#" class="logo">
                                <img src="../../assets/logo.png" alt="" style="">
                            </a>
                        </n-grid-item>
                        <n-grid-item>
                            <a class="link" href="#">
                                首页
                            </a>
                        </n-grid-item>
                        <n-grid-item>
                            <a class="link" href="#">
                                官方
                            </a>
                        </n-grid-item>
                        <n-grid-item>
                            <a class="link" href="#">
                                社区
                            </a>
                        </n-grid-item>
                        <n-grid-item>
                            <a class="link" href="#">
                                讨论
                            </a>
                        </n-grid-item>
                        <n-grid-item>
                            <a class="link" href="#">
