                    class="box"
                    inline
                    :rules="rules">
                    <n-form-item>
                        <n-input-group>
                            <n-input v-model:value="value" 
                                autosize
                                type="text" 
                                color="#252525"
                                placeholder="请输入内容"
                                class="search"/>
                            <n-button @click="handleClick" color="#252525">搜索</n-button>
                        </n-input-group>
                    </n-form-item>
                    <n-form-item>
                        <n-space>
                            <n-button-group class="box">
                                <n-button secondary strong @click="notify('success','个人信息')">
                                    <template #icon>
                                        <n-icon>
                                            <Person/>
                                        </n-icon>
                                    </template>
                                </n-button>
                            </n-button-group>
                            <n-button-group class="box">
                                <n-button color="#252525" @click="handleLogin">
                                    登录 | 注册
                                </n-button>
                            </n-button-group>
                            <n-button-group class="box">
                                <n-button secondary strong  @click="notify('success','设置')">
                                    <template #icon>
                                        <n-icon>
                                            <Settings/>
                                        </n-icon>
                                    </template>
                                </n-button>
                            </n-button-group>
                        </n-space>
                    </n-form-item>
                </n-form>
            </n-grid-item>
        </n-grid>
