</template>

<script>
import { defineComponent,reactive,ref } from 'vue'
import { useRouter } from "vue-router" 
import { Person,Settings } from "@vicons/ionicons5";

export default defineComponent({
    data() {
        return {
            formValue: {},
            value: ref(''),
            rules: ref({

            })
        }
    },  
    components: {
        Person,
        Settings
    },
    setup () {
        const router = useRouter()
        const state = reactive({

        })

        const handleLogin = () => {
            router.push({ path: '/login' })
        }

        const handleClick = () => {
            window.$message.success('搜索')
        }
        const notify = (type,msg) => {
            window.$notification[type]({
                content: '通知',
                meta: msg,
                duration: 2500,
                keepAliveOnHover: true
            })
        }
        return {
            state,
            handleClick,
            notify,
            handleLogin
        }
    }
})
</script>

<style lang="less" scoped>
.Header {
    height: 50px;
    background-color: var(--theme_white);
    .list {
        margin-left: 20px;
    }
    .search {
        min-width: 320px;
    }
}
.logo {
    img {
        width: 40px;
        height: 40px; 
        margin: 10px 0;
    }
}

.link,.box,.logo{
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--theme-font_black);
  text-decoration: none;
  transition: height 1s;    
}
.link:hover{
    height: 50px;
    border-bottom: 2px solid #252525;
    background-color: var(--theme_gray);
}
.box:last-child{
    justify-content: space-around;
}
</style>