import { createRouter, createWebHashHistory } from 'vue-router'
import Main from '../views/Main'
import Login from '../views/Login'

const routes = [
  {
    path: '/',
    name: 'home',
    component: Main
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
